/**
 *  - , 
 * @param fn {Function} 
 * @param interval {Number} 
 * @param isFirst {boolean} 
 * @return {Function}
 */
export const throttle = (fn, interval = 500, isFirst = true) => {
  // 
  let timer
  return function(...rest) {
    if (isFirst) {
      fn.apply(this, rest)
      isFirst = false
      return
    }
    if (timer) {
      return
    }
    timer = window.setTimeout(() => {
      clearTimeout(timer)

      fn.apply(this, rest)
    }, interval)
  }
}

export default throttle
