import isObject from './isObject.js'

/**
 * , ,
 * @param data
 * @param rest
 */
export const ban = (data, ...rest) => {
  let result = {}
  if (!isObject(data)) {
    return result
  }
  result = { ...data }
  rest.forEach(key => {
    delete result[key]
  })
  return result
}

export default ban
