import { isString } from './isString.js'

/**
 * 
 * @param string 
 */
export const prefix = string => {
  if (!isString(string)) {
    return ''
  }
  string = string.trim()

  const index = string.search(/^(\w+)+(?=\.)/)
  if (index === -1) {
    return ''
  }
  return RegExp.$1
}

export default prefix
