/**
 *  - , 
 * @param fn
 * @param interval 
 * @param isFirst 
 */
export const debounce = (fn, interval = 500, isFirst = false) => {
  let timer
  return function(...rest) {
    clearTimeout(timer)
    if (isFirst) {
      fn.apply(this, rest)
      isFirst = false
    }

    timer = setTimeout(() => {
      fn.apply(this, rest)
    }, interval)
  }
}

export default debounce
