import toNumber from './toNumber.js'

const fraction = ['', '']
const digit = ['', '', '', '', '', '', '', '', '', '']
const numberDigit = ['', '', '', '']
const unit = ['', '', '']
const rNumberDigit = /[]/g
const rUnit = /+([])/g
const rZero = /{2,}/

/**
 * 
 * @param n
 */
export const money2cn = n => {
  n = toNumber(n)
  let result = ''
  const integerAry = n
    .toString()
    .replace(/\.\d+$/, '')
    .split('')

  const decimalAry = n
    .toString()
    .replace(/^\d+\.?/, '')
    .split('')

  for (let i = integerAry.length - 1, j = 0; i >= 0; i--, j++) {
    if (integerAry[i] === '0' && i > 0 && i < integerAry.length - 1) {
      integerAry[i] = ''
    } else {
      integerAry[i] = digit[integerAry[i]] + numberDigit[j % 4]
    }
    integerAry[i] += j % 4 === 0 ? unit[j / 4] : ''
  }

  for (let i = 0; i < decimalAry.length; i++) {
    if (decimalAry[i] !== '0') {
      decimalAry[i] = digit[decimalAry[i]] + fraction[i]
    } else {
      decimalAry[i] = ''
    }
  }

  let integer = integerAry
    .join('')
    .replace(rNumberDigit, '')
    .replace(rUnit, '$1')
    .replace(rZero, '')

  if (integer.length > 1) {
    integer = integer.replace(/$/, '')
  }

  if (integer.length >= 1) {
    integer += ''
  }

  let decimal = decimalAry.join('')

  if (decimal === '') {
    decimal = ''
  } else {
    integer = integer.replace(/^$/, '')
  }

  result = integer + decimal

  return result
}

export default money2cn
